.vertical-timeline-element-content {
    background: unset;
    border-left: 3px solid #87d2ff;
    border-right: 3px solid #c99cfd;
    border-radius: 20;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background-position: 0 0, 0 100%;
    background-repeat: no-repeat;
    -webkit-background-size: 100% 20px;
    -moz-background-size: 100% 3px;
    background-size: 100% 3px;
    background-image: -webkit-linear-gradient(left, #87d2ff 0%, #c99cfd 100%),
        -webkit-linear-gradient(left, #87d2ff 0%, #c99cfd 100%);
    background-image: -moz-linear-gradient(left, #87d2ff 0%, #c99cfd 100%),
        -moz-linear-gradient(left, #87d2ff 0%, #c99cfd 100%);
    background-image: -o-linear-gradient(left, #87d2ff 0%, #c99cfd 100%),
        -o-linear-gradient(left, #87d2ff 0%, #c99cfd 100%);
    background-image: linear-gradient(to right, #87d2ff 0%, #c99cfd 100%),
        linear-gradient(to right, #87d2ff 0%, #c99cfd 100%);
    box-shadow: 0 .125rem .25rem rgba(0,0,0,.075) !important;
}

.vertical-timeline-element-content::before {
    border-left-color: #c99cfd;
}

.vertical-timeline-element:nth-child(2n):not(.vertical-timeline-element--left) .vertical-timeline-element-content::before {
    border-right-color: #87d2ff;
}


.vertical-timeline::before {
    background: -webkit-linear-gradient(left, #87d2ff 0%, #c99cfd 100%),
    -webkit-linear-gradient(left, #87d2ff 0%, #c99cfd 100%);
    background: -moz-linear-gradient(left, #87d2ff 0%, #c99cfd 100%),
        -moz-linear-gradient(left, #87d2ff 0%, #c99cfd 100%);
    background: -o-linear-gradient(left, #87d2ff 0%, #c99cfd 100%),
        -o-linear-gradient(left, #87d2ff 0%, #c99cfd 100%);
    background: linear-gradient(to right, #87d2ff 0%, #c99cfd 100%),
        linear-gradient(to right, #87d2ff 0%, #c99cfd 100%);
}

.vertical-timeline-element-date {
    margin-left: 4em;
    margin-right: 4em;
}