.monaco-editor {
  /* font-family: 'Nunito', sans-serif !important;
  font-weight: 600 !important; */
}

.box-label:not(:last-child) {
  margin-right: 0;
}

.rwt__tablist:not([aria-orientation="vertical"]) .rwt__tab[aria-selected="true"]::after {
  border-bottom: 3px solid #525f7f !important;
}

span.Resizer.vertical {
  width: 10px !important;
  cursor: ew-resize;
  background: transparent !important;
}

.react-monaco-editor-container {
  margin: 10px;
  border-radius: 8px;
  border: 8px solid #151516;
}

.description {
  font-weight: 600 !important;
}

.gradient-border {
  border-left: 3px solid #87d2ff;
  border-right: 3px solid #c99cfd;
  border-radius: 20 !important;
  -webkit-box-sizing: border-box !important;
  -moz-box-sizing: border-box !important;
  box-sizing: border-box !important;
  background-position: 0 0, 0 100% !important;
  background-repeat: no-repeat !important;
  -webkit-background-size: 100% 20px !important;
  -moz-background-size: 100% 3px !important;
  background-size: 100% 3px !important;
  background-image: -webkit-linear-gradient(left, #87d2ff 0%, #c99cfd 100%),
    -webkit-linear-gradient(left, #87d2ff 0%, #c99cfd 100%) !important;
  background-image: -moz-linear-gradient(left, #87d2ff 0%, #c99cfd 100%),
    -moz-linear-gradient(left, #87d2ff 0%, #c99cfd 100%) !important;
  background-image: -o-linear-gradient(left, #87d2ff 0%, #c99cfd 100%),
    -o-linear-gradient(left, #87d2ff 0%, #c99cfd 100%) !important;
  background-image: linear-gradient(to right, #87d2ff 0%, #c99cfd 100%),
    linear-gradient(to right, #87d2ff 0%, #c99cfd 100%) !important;
}



input[type='range'] {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  width: 100%;
  margin: 0 !important;
  padding: 0 !important;
  border: none;
  height: 24px;
  max-width: 400px;
  border-radius: 14px;  
  outline: none; /* no focus outline */
  background: linear-gradient(to right, #DEDDDF 0%, #EFEFEF 100%);
}

input[type='range']::-moz-range-track {
  border: inherit;
  background: transparent;
}

input[type='range']::-ms-track {
  border: inherit;
  color: transparent; /* don't drawn vertical reference line */
  background: transparent;
}

input[type='range']::-ms-fill-lower,
input[type='range']::-ms-fill-upper {
  background: transparent;
}

input[type='range']::-ms-tooltip {
  display: none;
}

/* thumb */

input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 60px;
  height: 24px;
  border: none;
  border-radius: 12px;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0%, white),
    color-stop(100%,  white)
  ); /* android <= 2.2 */
  background-image: -webkit-linear-gradient(
    top,
    #529de1 0,
    #245e8f 100%
  ); /* older mobile safari and android > 2.2 */
  background-image: linear-gradient(to bottom, #553F7A, #3E2863);
}
input[type='range']::-moz-range-thumb {
  width: 60px;
  height: 24px;
  border: none;
  border-radius: 12px;
  background-image: linear-gradient(to bottom, #553F7A, #3E2863);
}

input[type='range']::-ms-thumb {
  width: 60px;
  height: 24px;
  border-radius: 12px;
  border: 0;
  background-image: linear-gradient(to bottom, #553F7A, #3E2863);
}


.editor-pane {
  overflow-x: auto !important;
  overflow-y: hidden !important;
}
